import React from 'react';
import { connect } from 'react-redux';
import { getProfile, getUserStatus, updateStatus, uploadAvatar, updateProfileData} from '../../Redux/profileReducer';
import Profile from './Profile';
import { withRouter } from 'react-router';
import { AuthRedirect } from '../../HOC/AuthRedirect';
import { compose } from 'redux';



class ProfileContainer extends React.Component {

  refreshProfile = () => {
    let userId = this.props.match.params.userId;

      if (!userId) {
        userId = this.props.id;
      }

      this.props.getProfile(userId);
      this.props.getUserStatus(userId);
  }

  componentDidMount() {
      this.refreshProfile();
      
  }

  componentDidUpdate(prevProps) {
    if (prevProps.match.params.userId !== this.props.match.params.userId) {
      this.refreshProfile();
    }
  }

  

  render() {

      return (
        <Profile {...this.props} profile={this.props.profile} status={this.props.status} 
        updateStatus={this.props.updateStatus}
        isOwner={!this.props.match.params.userId}
        uploadAvatar={this.props.uploadAvatar}
        updateProfileData={this.props.updateProfileData} />
        
      )

  }
}


let mapStateToProps = (state) => {
  return {
    profile: state.profilePage.profile,
    status: state.profilePage.status,
    id: state.auth.userId,
    isAuth: state.auth.isAuth,
  }
}

let Container = compose(
  connect(mapStateToProps, {getProfile, getUserStatus, updateStatus, uploadAvatar, updateProfileData}),
  withRouter,
  AuthRedirect
)(ProfileContainer);



export default Container;


