import { Formik, Form} from 'formik';
import { MyTextInput, MyCheckbox } from '../../Common/Helpers/form_helper';


const ProfileDataForm = (props) => {
  return (

    <Formik
      initialValues={ props.profile }
      
      onSubmit={(values, { setSubmitting, setStatus}) => (
        props.updateProfileData(values, setStatus),
        props.closeEditMode(),      
        setSubmitting = false
      )}
    >
      {({ isSubmitting, status }) => (
        <Form>
          <MyTextInput
             label="Full name"
             name="fullName"
             type="text"
             
           />

          <MyCheckbox name="lookingForAJob" label="Looking for a job">
            Looking for a job?
          </MyCheckbox>

          <MyTextInput
             label="Professional skills"
             name="lookingForAJobDescription"
             type="text"
             //placeholder="password"
           />
           
           <MyTextInput
             label="About me"
             name="aboutMe"
             type="text"
             //placeholder="password"
           />

          <div><b>Contacts: </b>{Object.keys(props.profile.contacts).map(key => {            
            return <MyTextInput
                      label={key}
                      name={"contacts."+ key}
                      type="text"
                  />                 
            })} 
          </div>
          <div>{status}</div>      
          <div>
            <button type="submit" disabled={isSubmitting}>
              Save
            </button>
          </div>

        </Form>
      )}
    </Formik>
  );
}



export default ProfileDataForm;