import { authMeAPI } from "../Api/api";

const SET_USER_DATA = 'auth/SET_USER_DATA';



let initialState = {
  userId: null,
  email: null,
  login: null,
  isAuth: false
}

const authReducer = (state = initialState, action) => {



  switch (action.type) {
    case SET_USER_DATA:
      return {
        ...state,
        ...action.userData,

      }

    default:
      return state;
  }
}

export const setAuthUserData = (userId, email, login, isAuth) => {
  return {
    type: SET_USER_DATA,
    userData: { userId, email, login, isAuth }
  };
}

export const getAuthMeData = () => {
  return async (dispatch) => {

    let data = await authMeAPI.getAuthMeData();

    if (data.resultCode === 0) {
      let { id, email, login } = data.data;
      dispatch(setAuthUserData(id, email, login, true));
    }
  }
}

export const login = (email, password, rememberMe, setStatus) => {
  return async (dispatch) => {

    let data = await authMeAPI.login(email, password, rememberMe);

    if (data.resultCode === 0) {
      dispatch(getAuthMeData());
    } else {
      setStatus(data.messages[0]);
    }
  }
}

export const logout = () => {
  return async (dispatch) => {

    let data = await authMeAPI.logout();
    if (data.resultCode === 0) {
      dispatch(setAuthUserData(null, null, null, false));
    }
  }
}


export default authReducer;