import Preloader from '../../Common/Preloader/Preloader';
import ProfileStatusWithHooks from '../ProfileStatus/ProfileStatusWithHooks';
import classes from './ProfileInfo.module.css';
import userPhoto from '../../../assets/images/149071.png'
import { useState } from 'react';
import ProfileDataForm from './ProfileDataForm';


const ProfileInfo = (props) => {

  let [editMode, setEditMode] = useState(false);

  if (!props.profile) {
    return <Preloader />
  }

  const onMainPhotoSelected = (e) => {
    if (e.target.files.length) {
        props.uploadAvatar(e.target.files[0]);
    }
}

  const goToEditMode = () => setEditMode(true);
  const closeEditMode = () => setEditMode(false);

  return (
    <div>
      {/*<div className={classes.wall}>
        <img src='https://www.industrialempathy.com/img/remote/ZiClJf-1920w.jpg' />
  </div>*/}
      <div className={classes.descriptionBlock}>
        <span>
        <img src={props.profile.photos.large || userPhoto} />
          {props.isOwner && 
          <input type={"file"} onChange={onMainPhotoSelected}/>
          }
        </span>
        <span>
          <div className={classes.userName}>
            {props.profile.fullName}
          </div>
          <div>
          <ProfileStatusWithHooks status={props.status} updateStatus={props.updateStatus} />
          </div>
        </span>
      </div>
      {editMode ? 
      <ProfileDataForm profile={props.profile} updateProfileData={props.updateProfileData} closeEditMode={closeEditMode} /> : 
      <ProfileData profile={props.profile} isOwner={props.isOwner} goToEditMode={goToEditMode} />}
    </div>

  );
}

const ProfileData = (props) => {
  return (

<div>
  {props.isOwner &&
    <div>
      <button onClick={props.goToEditMode} >Edit Profile</button>
    </div>
  }
          <div><b>Looking for a job: </b>{props.profile.lookingForAJob ? 'yes' : 'no'} </div>
          {props.profile.lookingForAJob &&
          <div><b>My professional skills: </b>{props.profile.lookingForAJobDescription} </div>}
          {props.profile.aboutMe &&
          <div><b>About me: </b>{props.profile.aboutMe} </div>}
          <div><b>Contacts: </b>{Object.keys(props.profile.contacts).map(key => {
          //console.log(props.profile.contacts[key])
          if (props.profile.contacts[key]) 
           return <div className={classes.contact} ><b>{key}: </b>{props.profile.contacts[key]} </div>
          })} 
          </div>
        </div>
  )
}



export default ProfileInfo;